.nav_container {
  width: 100%;
  margin: 20px auto;
}

.center {
  text-align: center;
}

.nav h1 {
  margin-left: 20px;
  font-family: "forte";
  color: black;
}
.nav {
  box-shadow: 0px 2px 24px 0px rgba(0, 0, 0, 0.15);
  display: flex;
  justify-content: center;
  width: 100%;
}

.div_links {
  display: flex;
}

.nav_links {
  margin: auto;
}

.nav_links li {
  display: flex;
  border-bottom: 3px solid black;
  font-family: fantasy;
  font-size: 14px;
}

.nav_links a,
.nav a {
  text-decoration: none;
  color: white;
}

.nav li:hover {
  background-color: blue;
}

/* 
* For Average screens at least 720px 
*/
@media screen and (min-width: 720px) {
  .nav_links {
    display: flex;
    justify-content: center;
    font-size: 15px;
  }

  .nav_links li {
    border-bottom: none;
    padding: 28px 30px 27px 30px;
    display: inline;
    /*padding: 28px 5px 27px 5px; /* top right bottom left */
  }
}

